<template>
  <div class="keyboard">
    <div class="metro">
      <div class="carriage" id="getapp">
        <div class="container">
          <div class="image"></div>
          <div class="description">
            <div class="title">{{ $t('Georgian Keyboard')}}</div>
            <div class="text">{{ $t('Download full package of Georgian Alphabet from Anbani.ge')}}</div>
            <div class="downloadLinks">
              <div class="windows" v-on:click="getForWindows"></div>
              <div class="android" v-on:click="getForAndroid"></div>
            </div>
          </div>
        </div>
      </div>
      <div class="carriage" id="appscripts">
        <div class="container">
          <div class="description">
            <div class="title">{{ $t('Entire Georgian Alphabet')}}</div>
            <div class="text">{{ $t('Asomtavruli, Nuskhuri, Mkhedruli and Mtavruli')}}<span class="superscript">{{ $t('Soon')}}</span></div>
          </div>
          <div class="images">
            <div class="image surface"></div>
            <div class="image s8"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>



<style scoped>
  .keyboard {
    padding-bottom: 144px;
  }

  .metro {
    display: flex;
    flex-direction: column
  }

  .scripts {
    display: flex;
    flex-direction: row;
    height: 100%;
    align-items: center;
  }

  .scripts .script {
    font-size: 3vw;
    margin: 50px;
  }

  .carriage {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50%;
    z-index: 0;
    height: 500px;
    width: 100%;
    background-color: #eee;
  }

  .carriage .container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 100%;
  }

  .carriage:nth-child(2) {
    background-color: #fff;
  }

  #getapp {
    background-image: url("../assets/imgs/screen-1.jpg");
    box-shadow: inset 0 0 0 1000px rgba(40, 53, 147, .8);
  }


  .image {

    background-size: contain;
    background-repeat: no-repeat;
  }

  #getapp .image {
    height: 300px;
    width: 300px;
    background-image: url("../assets/imgs/applogo.png");
  }

  #getapp {
    height: 600px;
  }

  .description {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: auto;
  }

  .description .title {
    font-size: 3vw;
    color: #fff;
    font-weight: 900;
  }

  .description .text {
    font-size: 1.5vw;
    color: #fff;
    opacity: 0.7;
  }

  .description .readmore {
    background-color: #fff;
    color: #3F51B5;
  }

  .downloadLinks {
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-bottom: 15px;
  }

  .windows,
  .android {
    cursor: pointer;
    height: 60px;
    width: 191px;
    background-size: contain;
    background-repeat: no-repeat;
  }

  .windows {
    background-image: url("../assets/imgs/windows-badge.png");
  }

  .android {
    background-image: url("../assets/imgs/google-play-badge.png");
  }

  #appscripts {
    box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.2);
    z-index: 1;
  }

  #appscripts .images {
    position: relative;
    min-width: 600px;
    display: flex;
    flex-direction: row;
  }


  #appscripts .image.surface {
    width: 100%;
    height: 450px;
    background-image: url("../assets/imgs/surface.png");
  }

  #appscripts .image.s8 {
    right: 60px;
    top: 100px;
    position: absolute;
    height: 300px;
    width: 150px;
    background-image: url("../assets/imgs/s8.png");
  }

  #appscripts .description {
    align-items: flex-start;
  }

  #appscripts .text {
    font-size: 1.2vw;
  }

  #appscripts .title {
    font-size: 2.2vw;
  }


  .description .superscript {
    font-size: 0.5em;
    position: absolute;
  }

  #appscripts .description .text,
  #appscripts .description .title {
    color: #333;
  }

  #appandroid {
    background-color: #3f51b5
  }

  #appandroid .description .text
  {
    max-width: 600px;
    font-size: 1em;
    text-align: right;
  }

  #appandroid .image
  {
    background-image: url()
  }

  @media screen and (max-width: 950px){
    #getapp .container {
      flex-direction: column;
    }

    #appscripts .container {
      flex-direction: column;
    }

    #appscripts .images {
      padding: 70px;
      min-width: 100vw;
    }

    #appscripts {
      box-shadow: none;
    }

    #appscripts .description {
      margin-top: 50px;
      text-align: center;
      transform: scale(2)
    }
    #getapp .image {
      width: 50vw;
      margin-top:100px;
    }
  }

</style>

<script>
  export default {
    name: 'Keyboard',
    data() {
      return {
        msg: 'This is Keyboard'
      }
    },

    methods: {
      getForAndroid: function()
      {
        window.open("https://play.google.com/store/apps/details?id=ge.anbani.georgiankeyboard", "_blank");
      },

      getForWindows: function()
      {
        window.location.href = "/static/files/Georgian-Keyboards.zip";
      }
    }
  }

</script>


